	// 引入定位
import {addressData, seekCityData} from '../../common/list.js'
export default {
	name: 'citying',
	data() {
		return {
			citynone:true,
			address:'',
			citydata:[], //搜索的城市
			keywoeds:'',
			pageroute:'',  //从哪个页面进来的路由
			city: [
				{
				"name":'昆明市'
				},
				{
				"name":'大理白族自治州'
				},
				{
				"name":'北京市'
				},
				{
				"name":'上海市'
				},
				{
				"name":'广州市'
				},
				{
				"name":'深圳市'
				},
				{
				"name":'杭州市'
				}
				],

		};
	},
	methods:{
		searchCity(e){
			console.log(e)
			this.citynone = false
		},
		canCel(){
			this.citynone = true,
			this.keywoeds = '',
			this.citydata = []
		},
		addRess(){
			addressData()
			.then((res)=>{
				console.log(res)
				this.address = res.result.ad_info.city
				console.log(this.address)
				// 定位成功查询数据库取出该城市下的景点数据
			}).catch((err)=>{
				console.log('用户拒绝定位')
				this.address = '广州市'
			})
		},
		clickCity(){
			var city = this.address
			this.rouTes(city)
		},
		// 取到热门城市
		hotCity(city){
			// console.log(city)
			this.rouTes(city)
		},
		
		// 搜索城市
		seekCity(city){
			//console.log(city)
			this.rouTes(city)
		},
		
		searchInput(e){
			seekCityData(e.detail.value)
			.then((res) => {
				console.log(res)
				this.citydata = res
			}).catch((err) => {
				console.log(err)
			})
		},
		
		rouTes(city){
			if(this.pageroute == 'pages/travels/travels'){
				console.log(city)
				// 传给发表页面的
				this.$store.commit('travelmuta',city)
			}else{
				// 传给tabr攻略页面的
				console.log(city)
				this.$store.commit('citymuta',city)
			}
			uni.navigateBack({
				delta:1
			})
		}
	},
	mounted() {
		this.addRess()
	},
	// 判断路由
	onLoad() {
		let pages = getCurrentPages()
		let prevpage = pages[pages.length - 2]; 
		console.log(prevpage.route)
		this.pageroute = prevpage.route
	}
}
